<template>
	<div class="addCourse">
		<div class="pubHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}电子书</p>
			<div @click="checkAndUp()" class="selectStu">{{title}}</div>
		</div>
		<hr>
		<div v-if="show" style="position: absolute;padding-left: 35%;z-index: 9;background: white; padding-top: 10%; height: 50%;width: 43.5%;">
			 <el-progress type="circle" :percentage="currentRate"></el-progress>
		</div>
		<div class="content">
			<div class="mainInfo">
				<el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" label-position="left">
					<el-form-item label="书籍名" prop="name">
					    <el-input v-model="ruleForm.name" :maxlength="25" placeholder="请输入书籍名(1-25以内)"></el-input>
					</el-form-item>
					<el-form-item label="书籍简介" prop="desc">
					    <el-input type="textarea" :rows="5" v-model="ruleForm.desc" :maxlength="250" placeholder="请输入书籍简介（250字内）"></el-input>
					</el-form-item>
					<el-form-item label="导入书籍" prop="name">
						<div class="upBook">
							<el-upload
							  ref="book"
							  action="#"
							  :on-change="BohandleChange"
							  accept=".pdf,.txt,.epub"
							  :show-file-list="false"
							  :auto-upload="false">
							  <el-button size="small" type="primary">点击上传</el-button>
							</el-upload>
							<span>{{ruleForm.bookname}} </span>
						</div>
					</el-form-item>
				</el-form>
			</div>
			<div class="cover">
				<el-upload
				  ref="upload"
				  action="#"
				  list-type="picture-card"
				  :class="{disabled: isFine}"
				  accept=".jpg,.png,.jpeg"
  				  :on-remove="handleRemove"
  				  :on-change="handleChange"
				  :auto-upload="false">
				    <i slot="default" class="el-icon-plus"></i>
				</el-upload>
				<p style="margin-top: 15px;opacity: 0.8;"></p>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'addCourse',
	data(){
		var validateName = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('书名不能为空'));
	        } else {
	          callback();
	        }
	    };
	    var validatePwd = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('简介不能为空'));
	        } else {
	          callback();
	        }
	    };
		return {
      		param: new FormData(),
      		isFine: false,
      		title: '创建',
      		currentRate: 0,
      		show: false,
			ruleForm:{
				name: '',
				desc: '',
				cover:'',
				id: '',
				url: '',
				bookname: ''
			},
			old:{
				name: '',
				desc: '',
				cover:'',
				id: '',
				url: '',
				bookname: ''
			},
			rules: {
	          name: [
	            { validator: validateName, trigger: 'blur' }
	          ],
	          desc: [
	            { validator: validatePwd, trigger: 'blur' }
	          ]
	        },
	        isSave: false
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getOldInfo();
		}
	},
	//离开前提醒
	beforeRouteLeave(to, from , next) {
		if (!this.isSave) {
			if (this.$route.query.id) {
		 		(this.ruleForm.name != this.old.name || this.ruleForm.desc != this.old.desc || this.param.get('image') != null || this.param.get('book') != null)? next(confirm('确认不保存修改直接离开么？')) : next();
		 	} else {
		 		(this.ruleForm.name != '' || this.ruleForm.desc != '' || this.param.get('image') != null || this.param.get('book') != null) ? next(confirm('确认不保存修改直接离开么？')) : next();
		 	}
		 } else {
		 	next();
		 }
    },
	methods:{
		//返回上一页
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
	    //检验参数
	    checkAndUp(){
	    	var url = '';
	    	var ischange = false;
			this.param.set('token',this.getToken());
			//判断是否有传来的参数，有就是修改；没有就是发布
			if (this.$route.query.id) {
				if (this.ruleForm.name != this.old.name) {
					this.param.set('title',this.ruleForm.name);
					ischange = true;
				}
				if (this.ruleForm.desc != this.old.desc) {
					this.param.set('description',this.ruleForm.desc);
					ischange = true;
				}
				if (this.param.get('image')) {
					ischange = true;
				}
				if (this.param.get('book')) {
					ischange = true;
				}
				if (ischange) {
					this.param.set('id',this.ruleForm.id);
					url = this.$baseUrl+'/book/update';
				} else {
					this.$message.error('请确定有修改内容！');
					return ;
				}
			} else {
				if (this.ruleForm.name == '' || this.ruleForm.name.replace(/\s*/g, "").length ==0) {
		    		this.$message.error('书籍名不能为空！');
		    		return ;
		    	} 
		    	if (this.ruleForm.desc == '' || this.ruleForm.desc.replace(/\s*/g, "").length ==0) {
		    		this.$message.error('书籍简介不能为空！');
		    		return ;
		    	}
		    	if (this.param.get('image') == null) {
		    		this.$message.error('请上传封面图片！');
		    		return ;
		    	}
		    	if (this.param.get('book') == null) {
		    		this.$message.error('请导入电子书！');
		    		return ;
		    	}
		    	this.param.set('title',this.ruleForm.name);
		    	this.param.set('description',this.ruleForm.desc);
				url = this.$baseUrl+'/book/add';
			}
			// console.log(url)
			this.createCourse(url);
	    },
	    //创建/修改课程
	    createCourse(url){
	    	var that = this;
	    	//监控上传进度
	      	var config = {
		       onUploadProgress: progressEvent => {
		        var complete = (progressEvent.loaded / progressEvent.total * 100 | 0);
		        console.log('addBook ' + complete)
		        if (complete < 100) {
		          this.show = true;
		          this.currentRate = complete;
		        } 
		        if (complete > 98) {
		          this.currentRate = 98;
		        }
		       }
		     }
	    	this.axios.post(url,this.param,config)
	    	.then((res)=>{
	    		if (res.data.code == 200) {
	    			that.show = false;
	    			if (that.param.get('id')) {
	    				this.$store.commit('random',Math.random());
	    			}
	    			that.$message.success(that.title + '成功！');
	    			that.isSave = true;
	    			that.$router.go(-1);
	    		} else {
	    			that.$message.error(res.data.message);
	    		}
	    		//登录超时
	    		if (res.data.code == 403) {
	    			sessionStorage.removeItem("token");
	  				that.$message.error(res.data.message);
	          		that.$router.push('/');
	    		}
	    	})
	    	.catch((e)=>{
	    		console.log(e);
	    	})
	    },
		//删除图片
		handleRemove(file, fileList) {
	        // console.log(file, fileList);
	        console.log(fileList.length)
	        if (fileList.length < 1) {
	        	this.isFine = false;
	        }
	    },
	    //获取图片url
	    handleChange(file,fileList){
	    	console.log(file,file.size);
	    	if (file.size > 2 * 1024 * 1024) {
	    		this.$message.warning('请上传2MB以内图片！');
	    		this.$refs.upload.clearFiles();
	    		return ;
	    	}
	    	//获取上传的图片
	    	this.param.set('image',file.raw);
	    	console.log(fileList.length)
	    	if (fileList.length >= 1) {
	    		this.isFine = true;
	    	}
	    },
	    //获取图片url
	    BohandleChange(file,fileList){
	    	//获取上传的图片
	    	this.param.set('book',file.raw);
	    	this.ruleForm.bookname = file.name;
	    	var name = file.name.split(".");
	    	this.ruleForm.name = name[0];
	    },
	    //获取历史信息
	    getOldInfo(){
	    	var that = this;
	    	var query = this.$route.query;
	    	if (query.id) {
	    		this.title = '修改'
	    		this.ruleForm.name = query.name;
	    		this.ruleForm.desc = query.desc;
	    		this.ruleForm.id = query.id;
	    		//保存数据
	    		this.old.name = query.name;
	    		this.old.desc = query.desc;
	    		this.old.id = query.id;
	    	}
	    }
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.addCourse{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 0;
	}
	.pubHead{
		width: 100%;
		height: 50*@allwidth;	
		font-size: 19*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.selectStu{
			margin-left: auto;
			text-align: center;
			padding: 0 20*@allwidth;
			height: 38*@allwidth;
			line-height: 38*@allwidth;
			color: #fff;
			background: #226352;
			cursor: pointer;
			border-radius: 5px;
			font-size: 19*@allwidth;
		}
	}
	.content{
		display: flex;
		align-items: flex-start;
		margin-top: 20*@allwidth;
		.mainInfo{
			width: 65%;
		}
		.cover{
			width: 35%;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}
	
}
.disabled .el-upload--picture-card{
    display: none;
    background: red;
}
.codew{
	display: flex;
	align-items: center;
	justify-content: space-between;
	li{
		width: 55%;
		display: flex;
		align-items: center;
		p{
			width: 100px;
		    font-size: 0.373333rem;
		    color: #606266;
		    line-height: 1.066667rem;
		    padding: 0 0.32rem 0 0;
		}
	}
	li:nth-child(2){
		width: 40%;
		p{
			width: 60px;
		}
	}
}
.upBook{
	display: flex;
	span{
		font-size: 18*@allwidth;
		margin-left: 10*@allwidth;
	}
}
</style>