<template>
	<div class="showInform">
		<div class="showHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}详情页</p>
			<div class="annouce" v-show="ifUP">
				<div @click="goCorrect()" class="selectStu">修改</div>
			</div>
		</div>
		<hr>
		<div class="content" v-if="type == 'book'? false:true">
			<div class="newsTitle">{{showData.newsTitle}}</div>
			<div class="author">
				<p v-if="name == 'news'">发布人：{{showData.publisher}}</p>
				<p>发布时间：{{DateTranse(showData.publishTime)}}</p>
			</div>
			<div class="newscontent" v-html="showData.newsContent"></div>
		</div>
		<div class="Book" v-else>
			<div class="mainInfo">
				<el-form label-width="100px" class="demo-ruleForm" label-position="left">
					<el-form-item label="书籍名">
					    <div class="BName">
					    	{{BooKData.name}}
					    </div>
					</el-form-item>
					<el-form-item label="书籍简介" prop="desc">
					     <div class="BDesc">
					    	{{BooKData.desc}}
					    </div>
					</el-form-item>
					<el-form-item label="下载书籍" prop="name">
						<div style="display: flex;">
							<a :href="imgUrl + '/book/' + BooKData.url" :download="BooKData.name">
								<el-button size="small" type="primary"><span>电子书下载</span> </el-button>
							</a>
							<!-- <div @click="dialogTableVisible = true">预览</div> -->
						</div>
					</el-form-item>
				</el-form>
			</div>
			<div class="cover">
				<div class="ShowImg" v-if="BooKData.cover">
					<img :src="imgUrl + '/course/' + BooKData.cover">
				</div>
			</div>
		</div>
		<!-- <el-dialog title="预览" width="90%" :visible.sync="dialogTableVisible">
			<pdf :url="imgUrl + '/book/' + BooKData.url"></pdf>
		</el-dialog> -->
	</div>
</template>

<script>
// import pdf from '../components/preview.vue'
export default {
	name: 'showInform',
	// components: {
	//     pdf
	// },
	mounted(){
		if (this.getToken()) {
			let query = this.$route.query;
			// console.log(query.name,query.id);
			this.getContent(query.name,query.id);
			this.imgUrl = this.$ImgUrl;
			this.id = query.id;
			if (query.name == 'book') {
				this.type = 'book';
			}
			// console.log(this.type)
		}
	},
	methods:{
		//添加展示样式
		addStyle(str1){
			var str2 = '';
			// console.log(str1);
			str2 = str1.replace(/<img/g,'<img style="width: 100%;height:100%; margin: 1.4987013vw 0;"');
			str2 = str2.replace(/<p class/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 2vw;" class');
      		str2 = str2.replace(/<p>/g,'<p style="text-indent: 2em;margin: 0;font-size: 1.3vw;line-height: 2vw;">');
			this.showData.newsContent = str2;
		},
		//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		theToken = theToken.slice(1,theToken.length-1);
    		// console.log(theToken);
    		return theToken;
    	},
    	//转换日期格式
		DateTranse(data){
			var d = new Date(data);
			var hour = d.getHours() < 10 ? '0'+ d.getHours():d.getHours();
			var minu = d.getMinutes() < 10 ? '0'+ d.getMinutes():d.getMinutes();
			var sec = d.getSeconds() < 10 ? '0'+ d.getSeconds():d.getSeconds();
			var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + hour + ':' + minu + ':' + sec;
			return datetime;
		},
		//获取展示页面类型和ID 
		getContent(name,id){
			var that = this;
			switch(name){
				case 'news':
					this.title = '新闻';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/article/'+id;
					break;
				case 'article':
					this.title = '文章';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/article/'+id;
					break;
				case 'analysis':
					this.title = '案例分析';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/article/'+id;
					break;
				case 'book':
					this.title = '电子书';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/book/'+id;
					break;
				case 'inform':
					this.title = '通知';
					this.getUrl = this.$baseUrl + '/notice/'+ id;
					break;
				case 'test':
					this.ifUP = true;
					this.title = '考试';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				case 'homework':
					this.ifUP = true;
					this.title = '作业';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				default :
					break;
			}
			this.name = name;
			this.param.set('token',this.getToken());
			this.getDetail(this.getUrl);
		},
		//获取详细信息
		getDetail(url){
			var that = this;
			this.axios.post(url,this.param)
			.then((res)=>{
				console.log(res.data)
				if (res.data.code == 200) {
					res = res.data.data;
					console.log(this.type == 'book')
					if (that.type == 'book') {
						console.log(res.title)
						that.BooKData.id = res.id;
						that.BooKData.name = res.title;
						that.BooKData.desc = res.description;
						that.BooKData.cover = res.image;
						that.BooKData.url = res.book;
						that.pdfUrl = res.book;
					} else {
						that.showData.newsTitle = res.title;
						that.showData.publisher = res.publisherName;
						that.showData.publishTime = res.time;
						that.showData.ID = res.id;
						that.addStyle(res.content);
					}
				} else {
					that.$message.error('获取内容失败！');
				}
				//登录超时
	    		if (res.data.code == 403) {
	    			sessionStorage.removeItem("token");
	  				that.$message.error(res.data.message);
	          		that.$router.push('/');
	    		}
			})
		},
		//返回上一页
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //进行修改
	    goCorrect(){
	    	if (this.type == 'book') {
	    		this.$router.push({
					path: `/Bookadd`,
					query:{
						id: this.BooKData.id,
						name: this.BooKData.name,
						desc: this.BooKData.desc,
					}
				})
	    	} else {
	    		this.$router.push({
					path: `/Publish`,
					query:{
						name: 'Re' + this.name,
						id: this.showData.ID,
						// theParam: this.param
					}
				})
	    	}
	    },
	    //获取富文本里的第一张图片
	    imgUrlFun(str){
	        var data = '';
            str.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/, function (match, capture) {
                  data =  capture;
            });
            var result = data ? data: 'no'
            console.log(result);
	        // return data;
	    },
	    //获取内容简介(前30个字)
	    filterHTMLTag(str) {
	        str = str.replace(/<\/?[^>]*>/g,''); //去除HTML tag
	        str = str.replace(/[ | ]*\n/g,'\n'); //去除行尾空白
	        str = str.replace(/ /ig,'');//去掉 
	        console.log(str)
	        var endlen = str.length >= 30 ? 30 : str.length;
	        var desc = str.slice(0,endlen);
	        console.log(desc,desc.length)
	        // return str;
		}
	},
	data(){
		return {
      		param: new FormData(),
			title: '',
			getUrl:'',
			name: '',
			ifUP: false,
      		showData:{
      			ID: '',
      			newsTitle: '',
      			newsContent: '',
      			publisher: '',
      			publishTime: ''
      		},
      		isbook: false,
			imgUrl: '',
			id: '',
			type: '',
			BooKData:{
				id: '',
				name: '',
				cover: '',
				desc: '',
				url: ''
			},
			// dialogTableVisible: false,
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.showInform{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.7px solid rgb(61, 126, 141);
		padding: 0;
		margin: 0;
		margin-bottom: 10*@allwidth;
	}
	.showHead{
		height: 50*@allwidth;	
		font-size: 19*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.annouce{
			margin-left: auto;
			display: flex;
			.selectStu{
				text-align: center;
				height: 38*@allwidth;
				line-height: 38*@allwidth;
				width: 80*@allwidth;
				color: #fff;
				background: #54CAAB;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 20*@allwidth;
			}
		}
	}
	.content{
		width: 95%;
		margin: 0 auto;
		height: calc(100vh - 190*@allwidth);
		padding-bottom: 20*@allwidth;
		overflow-x: hidden;
		overflow-y: auto;
		span{
			font-size: 18*@allwidth;
			text-indent: 2em;
			line-height: 20*@allwidth;
			font-size: 1.2987013vw;
		    text-indent: 2em;
		    line-height: 1.44300144vw;
		}
		.newsTitle{
			font-size: 25*@allwidth;
			line-height: 50*@allwidth;
			font-weight: bold;
			text-align: center;
		}
		.author{
			margin-left: 59*@allwidth;
			display: flex;
			justify-content: flex-end;
			p{
				margin: 0 20*@allwidth;
				font-size: 16*@allwidth;
				opacity: 0.7;
			}
		}
	}
	.content::-webkit-scrollbar{
		display: none;
	}
}
.Book{
	margin: 15*@allwidth;
	display: flex;
	justify-content: space-between;
	border: 1px solid #D2D2D2;
	border-radius: 10*@allwidth;
	padding: 30*@allwidth 20*@allwidth;
	padding-bottom: 10*@allwidth;
	.mainInfo{
		width: 70%;
		.BName{
			background-color: #FFF;
		    border-radius: 0.106667rem;
		    border: 0.026667rem solid #DCDFE6;
		    color: #606266;
		    display: inline-block;
		    font-size: inherit;
		    height: 1.066667rem;
		    line-height: 1.066667rem;
		    padding: 0 0.2rem;
		    //超出部分省略
		    white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			word-break: break-all;

		}
		.BDesc{
			padding: 0.133333rem 0.4rem;
		    line-height: 1.5;
		    color: #606266;
		    background-color: #FFF;
		    border: 0.026667rem solid #DCDFE6;
		    border-radius: 0.106667rem;
		    -webkit-transition: border-color .2s cubic-bezier(.645,.045,.355,1);
		    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
		    height: 3rem;
		    overflow-y: auto;
		}
		a{
			color: #409EFF;
			font-size: 20*@allwidth;
			text-decoration: none;
			i span{
				font-size: 16*@allwidth;
				margin-left: 2px;
			}
		}
		/deep/ .el-form-item{
			margin-bottom: 0.22rem;
		}
	}
	.cover{
		width: 35%;
		display: flex;
		flex-direction: column;
		align-items: center;
		.ShowImg{
			background-color: #fbfdff;
		    border: 0.026667rem dashed #c0ccda;
		    border-radius: 0.16rem;
		    box-sizing: border-box;
		    width: 3.946667rem;
		    height: 3.946667rem;
		    line-height: 3.893333rem;
		    vertical-align: top;
		    img{
		    	height: 100%;
		    	width: 100%;
		    }
		}
	}
}
.pdf{
	span{
		width: 100%;
	}
}
.BDesc::-webkit-scrollbar{
	width: 0;
}
</style>