<template>
  <div class="news">
  	<div class="newsHead">
  		<ul class="tabs">
  			<li style="background: #90a7a6;" @click="$router.push('/News/news')">新闻</li>
  			<li>资料</li>
  		</ul>
  		<ul class="add">
  			<li @click="publish('article')">添加文章</li>
  			<li @click="publish('analysis')">添加案例</li>
  			<li @click="$router.push('/Bookadd')">添加电子书</li>
  		</ul>
  	</div>
	<div :style="{display: noData? 'none':'block'}">
  		<div class="showList">
	  		<el-row v-for="(list,index) in showList" :key="index">
			  	<el-col :span="20" @click.native="showDetail(index)">
				  	<div class="main">
				  		<p>
				  			{{list.title}}
				  			<!-- <el-tag type="danger">电子书</el-tag> -->
				  			<span v-if="list.type == 'book'" style="font-size: 5px;font-weight: normal;border: 1px solid rgb(144, 167, 166); padding: 2px 3px; color: #099c4a;">电子书</span>
				  			<span v-if="list.type == 'article'" style="font-size: 5px;font-weight: normal;border: 1px solid rgb(144, 167, 166); padding: 2px 3px; color: #409EFF;">文章</span>
				  			<span v-if="list.type == 'analysis'" style="font-size: 5px;font-weight: normal;border: 1px solid rgb(242, 130, 106); padding: 2px 3px; color: rgb(242, 130, 106);">案例分析</span>
				  		</p>
				  		<p>发布人：{{list.author}} </p>
				  		<p>发布时间：{{list.time}}</p>
				  	</div>
			  	</el-col>
			  	<el-col :span="3"></el-col>
			  	<el-col :span="1">
				  	<div class="operate">
				  		<i @click="ifSureDo(index)" class="el-icon-delete"></i>
				  	</div>
			  	</el-col>
			</el-row>
	  	</div>
	  	<div class="paging">
	  	 <!-- ref="pagination" -->
	  		<el-pagination
	  		  v-if='ispage'
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="8"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
  	</div>
  	<div :style="{display: noData? 'block':'none'}">
  		<div class="notice">
	  		<img src="../assets/img/nodata.png">
	  		<p>暂无内容！</p>
	  	</div>
  	</div>
  </div>
</template>

<script>
export default{
	name: 'news',
	data() {
      return {
      	activePage: 1,
      	totalPage: 1,
      	ispage: false,
      	getDataUrl: '',
      	title: '',
      	name: '',
      	param: new FormData(),
        showList:[],
        noData: false,
        navBar:['文章','电子书','判例分析'],
        baractive: 0
      };
    },
    //立即执行函数
    mounted(){
    	if (this.getToken()) {
			this.getName();
		}
    },
    methods: {
		//获取新闻或通知类别
		getName(){
			//获取当前页
			var nowPage = sessionStorage.getItem('nowPage');
			// console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.set('isApp',0);
			this.param.set('type','article');
			this.getDataUrl = this.$baseUrl + '/article/search'
			this.param.set('token',this.getToken());
			this.param.set('pageNum',8);
			this.param.set('currentPage',this.activePage);
			this.getNewsList(this.getDataUrl);
		},
		//获取信息列表
		getNewsList(url){
			var that = this;
			this.axios.post(url,this.param)
	      	.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.showList = [];
		        //为什么不能用箭头函数？
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		publisher: perData.publisherId,
		        		time: perData.time,
		        		author: perData.publisherName,
		        		type: perData.type
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	// console.log(res.data);
		        	console.log(res);
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.showList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '列表为空！');
		        		that.noData = true;
		        	}
		        	//加载分页
		        	if (res.data.totalNumber > 8) {
		        		that.ispage = true;
		        	}
		        } else {
		        	that.$message.error(res.message);
		        }
		        //登录超时
	    		if (res.code == 403) {
	    			sessionStorage.removeItem("token");
	  				that.$message.error(res.message);
	          		that.$router.push('/');
	    		}
		     })
	      	.catch((e)=>{
	      		console.log(e)
		     })
		},
    	//获取当前页信息
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.param.set('currentPage',this.activePage);
			this.getNewsList(this.getDataUrl);
			//保存页码
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//进入每条列表的详情页
		showDetail(index){
			this.$router.push({
				path: `/Show`,
				query:{
					name: this.showList[index].type,
					id: this.showList[index].id
				}
			});
		},
		//进入对应新闻/通知的发布页面
		publish(name){
			this.$router.push({
				path: `/Publish`,
				query:{
					name: name
				}
			});
		},
		ifSureDo(index){
			var that = this;
			this.$confirm('确认删除该条信息？')
			.then(()=>{
				that.deleteList(index)
			})
			.catch(()=>{})
		},
		// 删除该条信息
		deleteList(index){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl = '';
			switch(this.showList[index].type){
				case 'article':
					DdataUrl = this.$baseUrl + '/article/delete';
					Dparam.set('id',this.showList[index].id);
					Dparam.set('publisherId',this.showList[index].publisher);
					break;
				case 'analysis':
					DdataUrl = this.$baseUrl + '/article/delete';
					Dparam.set('id',this.showList[index].id);
					Dparam.set('publisherId',this.showList[index].publisher);
					break;
				case 'book':
					DdataUrl = this.$baseUrl + '/book/delete';
					Dparam.set('id',this.showList[index].id);
					break;
				default :
				 	break;
			}
			Dparam.set('token',this.getToken());
			console.log(this.showList[index].id);
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				if (res.data.code == 200) {
					this.getNewsList(this.getDataUrl);
					that.$message.success('删除成功！');
				} else {
					that.$message.error('删除失败！');
				}
				//登录超时
	    		if (res.data.code == 403) {
	    			sessionStorage.removeItem("token");
	  				that.$message.error(res.data.message);
	          		that.$router.push('/');
	    		}
			})
		},
    	//获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	choseBar(index){
    		this.baractive = index;
    	}
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
ul{
	margin: 0;
	padding: 0;
}
.news{
	width: 98%;
	margin: 0 auto;
	.newsHead{
		height: 48*@allwidth;
		padding-bottom: 6*@allwidth;
		border-bottom: 1.5px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			min-width: 65*@allwidth;
			height: 38*@allwidth;
			line-height: 38*@allwidth;
			padding: 0 10*@allwidth;
			margin: 0 3*@allwidth;
			font-size: 19*@allwidth;
			color: #fff;
			background: #099c4a;
			border-radius: 5px;
			cursor: pointer;
		}
		.tabs{
			display: flex;
			line-height: 45*@allwidth;
		}
		.add{
			margin-left: auto;
			line-height: 45*@allwidth;
			display: flex;
			li{
				color: #22A991;
				background: #E7FFF7;
				border: .6px solid #22A991;
			}
		}
	}
	.showList{
		padding-top: 13*@allwidth;
		.el-row{
			height: 45*@allwidth;
			border: 1px solid grey;
			border-radius: 5px;
			font-size: 14*@allwidth;
			padding: 0 20*@allwidth;
			margin-bottom: 4*@allwidth;
			color: #999999;
			display: flex;
			align-items: center;
			cursor: pointer;
			.main{
				display: flex;
				align-items: center;
				p{
					font-size: 16*@allwidth;
					color: #999999;
					margin-left: 30*@allwidth;
				}
				p:first-child{
					font-size: 18*@allwidth;
					color: #656565;
					font-weight: bold;
					margin-left: 0;
				}
			}
			.intro{
				// 文字超出后显示省略号
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
			.operate{
				display: none;
				font-size: 25*@allwidth;
				margin-left: 16*@allwidth;
				i{
					padding: 0 4*@allwidth;
				}
				i:hover{
					color: red;
					opacity: 0.7;
				}
			}
		}
		.el-row:hover{
			border: 2px solid #58CD77;
			.operate{
				display: block;
			}
		}
	}
	.paging{
		margin-top: 6*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
	.notice{
		width: 99%;
		margin: 10*@allwidth auto;
		height: calc(100vh - 190*@allwidth);
		min-height: 200px;
		box-shadow: 0 0 0.3px 0.3px #5D8486;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		img{
			width: 70px;
			height: 70px;
		}
		p{
			font-size: 18*@allwidth;
			margin: 5*@allwidth;
			color: #5D8486;
		}
	}
}
</style>