<template>
	<div class="addCourse">
		<div class="pubHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}课程</p>
			<div @click="checkAndUp()" class="selectStu">{{title}}</div>
		</div>
		<hr>
		<div class="content">
			<div class="mainInfo">
				<el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" label-position="left">
					<el-form-item label="课程名" prop="name">
					    <el-input v-model="ruleForm.name" :maxlength="20" placeholder="请输入课程名(1-20以内)"></el-input>
					</el-form-item>
					<el-form-item label="课程简介" prop="desc">
					    <el-input type="textarea" :rows="5" v-model="ruleForm.desc" :maxlength="100" placeholder="请输入课程简介（100字内）"></el-input>
					</el-form-item>
				</el-form>
				<ul class="codew">
					<li>
						<p>邀请码</p>
						<el-input v-model="ruleForm.invite" :maxlength="6"  oninput="value=value.replace(/[^\d]/g,'')" placeholder="请设置6位数字邀请码"></el-input>
					</li>
					<li>
						<p>权重</p>
						<el-input v-model="ruleForm.weights" :maxlength="2"  oninput="value=value.replace(/[^\d]/g,'')" placeholder="权重0-99"></el-input>
					</li>
				</ul>
				<div>
					
				</div>
			</div>
			<div class="cover">
				<el-upload
				  ref="upload"
				  action="#"
				  list-type="picture-card"
				  :class="{disabled: isFine}"
  				  :on-remove="handleRemove"
  				  :on-change="handleChange"
  				  accept="image/*"
				  :auto-upload="false">
				    <i slot="default" class="el-icon-plus"></i>
				</el-upload>
				<p style="margin-top: 15px;opacity: 0.8;">上传课程封面（2MB以内）</p>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'addCourse',
	data(){
		var validateName = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('课程名不能为空'));
	        } else {
	          callback();
	        }
	    };
	    var validatePwd = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('简介不能为空'));
	        } else {
	          callback();
	        }
	    };
	    var validateInvite = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error('邀请码不能为空'));
	        } else if(value.length < 6) {
	        	callback(new Error('请输入6位邀请码！'));
	        }
	         else {
	          callback();
	        }
	    };
		return {
      		param: new FormData(),
      		isFine: false,
      		title: '创建',
			ruleForm:{
				name: '',
				desc: '',
				cover:'',
				id: '',
				invite: '',
				weights: 1,
			},
			old:{
				name: '',
				desc: '',
				cover:'',
				id: '',
				invite: '',
				weights: ''
			},
			rules: {
	          name: [
	            { validator: validateName, trigger: 'blur' }
	          ],
	          desc: [
	            { validator: validatePwd, trigger: 'blur' }
	          ],
	          invite: [
	            { validator: validateInvite, trigger: 'blur' }
	          ]
	        },
	        isSave: false
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getOldInfo();
		}
	},
	//离开前提醒
	beforeRouteLeave(to, from , next) {
		if (!this.isSave) {
			if (this.$route.query.id) {
		 		(this.ruleForm.name != this.old.name || this.ruleForm.desc != this.old.desc || this.ruleForm.invite != this.old.invite || this.ruleForm.weights != this.old.weights || this.param.get('pic') != null)? next(confirm('确认不保存修改直接离开么？')) : next();
		 	} else {
		 		(this.ruleForm.name != '' || this.ruleForm.desc != '' || this.ruleForm.invite != '' || this.ruleForm.weights != 1 || this.param.get('pic') != null) ? next(confirm('确认不保存修改直接离开么？')) : next();
		 	}
		 } else {
		 	next();
		 }
    },
	methods:{
		//删除图片
		handleRemove(file, fileList) {
	        // console.log(file, fileList);
	        console.log(fileList.length)
	        if (fileList.length < 1) {
	        	this.isFine = false;
	        }
	    },
		//返回上一页
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //获取token验证,token 不能有" "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
	    //检验参数
	    checkAndUp(){
	    	var url = '';
	    	var ischange= false;
			this.param.set('token',this.getToken());
			//判断是否有传来的参数，有就是修改；没有就是发布
			if (this.$route.query.id) {
				if (this.ruleForm.name != this.old.name) {
					this.param.set('title',this.ruleForm.name);
					ischange = true;
				}
				if (this.ruleForm.desc != this.old.desc) {
					this.param.set('description',this.ruleForm.desc);
					ischange = true;
				}
				console.log(this.ruleForm.invite,this.old.invite)
				if (this.ruleForm.invite != this.old.invite) {
					this.param.set('code',this.ruleForm.invite);
					ischange = true;
				}
				if (this.ruleForm.weights != this.old.weights) {
					this.param.set('weights',this.ruleForm.weights);
					ischange = true;
				}
				if (this.param.get('pic')) {
					ischange = true;
				}
				if (ischange) {
					this.param.set('id',this.ruleForm.id);
					url = this.$baseUrl+'/course/update';
				} else {
					this.$message.error('请确定有修改内容！');
					return ;
				}
				
			} else {
				if (this.ruleForm.name == '' || this.ruleForm.name.replace(/\s*/g, "").length ==0) {
		    		this.$message.error('课程名不能为空！');
		    		return ;
		    	} 
		    	if (this.ruleForm.desc == '' || this.ruleForm.desc.replace(/\s*/g, "").length ==0) {
		    		this.$message.error('课程简介不能为空！');
		    		return ;
		    	}
		    	// console.log('this code : ' +  this.ruleForm.invite)
		    	if (this.ruleForm.invite.length != 6 ) {
		    		this.$message.error('请输入6位课程邀请码！');
		    		return ;
		    	}
		    	if (this.ruleForm.weights == '' || this.ruleForm.weights.replace(/\s*/g, "").length ==0) {
		    		this.$message.error('权重不能为空！');
		    		return ;
		    	}
		    	if (this.param.get('pic') == null) {
		    		this.$message.error('请上传封面图片！');
		    		return ;
		    	}
		    	this.param.set('title',this.ruleForm.name);
		    	this.param.set('code',this.ruleForm.invite);
		    	this.param.set('weights',this.ruleForm.weights);
		    	this.param.set('description',this.ruleForm.desc);
				url = this.$baseUrl+'/course/create';
			}
			// console.log(url)
			this.createCourse(url);
	    },
	    //创建/修改课程
	    createCourse(url){
	    	var that = this;
	    	this.axios.post(url,this.param)
	    	.then((res)=>{
	    		if (res.data.code == 200) {
	    			if (that.param.get('id')) {
	    				this.$store.commit('random',Math.random());
	    			}
	    			that.$message.success(that.title + '成功！');
	    			that.isSave = true;
	    			that.$router.go(-1);
	    		} else {
	    			that.$message.error(that.title + '失败！');
	    		}
	    		//登录超时
	    		if (res.data.code == 403) {
	    			sessionStorage.removeItem("token");
	  				that.$message.error(res.data.message);
	          		that.$router.push('/');
	    		}
	    	})
	    	.catch((e)=>{
	    		console.log(e);
	    	})
	    },
	    //获取图片url
	    handleChange(file,fileList){
	    	console.log(file,file.size);
	    	if (file.size > 2 * 1024 * 1024) {
	    		this.$message.warning('请上传2MB以内图片！');
	    		this.$refs.upload.clearFiles();
	    		return ;
	    	}
	    	//获取上传的图片
	    	this.param.set('pic',file.raw);
	    	console.log(fileList.length)
	    	if (fileList.length >= 1) {
	    		this.isFine = true;
	    	}
	    },
	    //获取历史信息
	    getOldInfo(){
	    	var that = this;
	    	var query = this.$route.query;
	    	if (query.id) {
	    		this.title = '修改'
	    		this.ruleForm.name = query.name;
	    		this.ruleForm.id = query.id;
	    		this.ruleForm.cover = query.cover;
	    		this.ruleForm.desc = query.desc;
	    		this.ruleForm.invite = query.code;
	    		this.ruleForm.weights = query.weights;
	    		//保存数据
	    		this.old.name = query.name;
	    		this.old.id = query.id;
	    		this.old.cover = query.cover;
	    		this.old.desc = query.desc;
	    		this.old.invite = query.code;
	    		this.old.weights = query.weights;
	    	}
	    }
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.addCourse{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 0;
	}
	.pubHead{
		width: 100%;
		height: 50*@allwidth;	
		font-size: 19*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.selectStu{
			margin-left: auto;
			text-align: center;
			padding: 0 20*@allwidth;
			height: 38*@allwidth;
			line-height: 38*@allwidth;
			color: #fff;
			background: #226352;
			cursor: pointer;
			border-radius: 5px;
			font-size: 19*@allwidth;
		}
	}
	.content{
		display: flex;
		align-items: flex-start;
		margin-top: 20*@allwidth;
		.mainInfo{
			width: 65%;
		}
		.cover{
			width: 35%;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}
	
}
.disabled .el-upload--picture-card{
    display: none;
    background: red;
}
.codew{
	display: flex;
	align-items: center;
	justify-content: space-between;
	li{
		width: 55%;
		display: flex;
		align-items: center;
		p{
			width: 100px;
		    font-size: 0.373333rem;
		    color: #606266;
		    line-height: 1.066667rem;
		    padding: 0 0.32rem 0 0;
		}
	}
	li:nth-child(2){
		width: 40%;
		p{
			width: 60px;
		}
	}
}
</style>